/* Style for the contact container */
.contact-container {
  max-width: 400px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Style for the contact heading */
.contact-heading {
  color: #333;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Style for the form labels */
label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

/* Style for the form inputs */
input[type="text"],
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/* Style for the submit button */
.submit-button {
  background-color: #4caf50;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Hover effect for the submit button */
.submit-button:hover {
  background-color: #45a049;
}
